/*---------------------------------
    $.custominputfile
---------------------------------*/
.cif-wrapper > .hide {
  display: none;
}
.cif-wrapper > .cif-group {
  position: relative;
  overflow: hidden;
  display: block;
}
.cif-wrapper > .cif-group > .cif-text {
  z-index: 10;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857;
  color: #555555;
  background-color: #FFFFFF;
  background-image: none;
  border: 1px solid #CCCCCC;
}
.cif-wrapper > .cif-group > .cif-text[readonly] {
  background-color: #EEEEEE;
}
.cif-wrapper > .cif-group > .cif-btn {
  outline: 0;
  z-index: 10;
  cursor: pointer;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  line-height: 1.42857;
  margin-bottom: 0;
  padding: 7px 15px 6px;
  color: #555555;
  background-color: #EEEEEE;
  font-size: 14px;
  font-weight: normal;
  text-align: center;
  border: 1px solid #D3D3D3;
  vertical-align: middle;
  white-space: nowrap;
  touch-action: manipulation;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: all 0.5s ease 0s;
  -moz-transition: all 0.5s ease 0s;
  -ms-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.cif-wrapper > .cif-group > .cif-btn:hover, .cif-wrapper > .cif-group > .cif-btn:active, .cif-wrapper > .cif-group > .cif-btn:focus {
  color: #FFFFFF;
  background-color: #00ACC1;
  border-color: #00ACC1;
}

.lm-cif {
  padding-left: 12px !important;
}

.lm-cif-btn {
  z-index: 10;
  position: absolute;
  top: 0;
  right: 0;
  color: #555555 !important;
  background-color: #EEEEEE !important;
  border: 1px solid #D3D3D3;
  box-shadow: none;
}

.lm-cif-btn:hover {
  color: #FFFFFF !important;
  background-color: #00ACC1 !important;
  border: 1px solid #00ACC1;
  border-left: 0px none;
}
